/*** modal styles ***/
.ComparisonModal__modal_container__ixRia {
    position: fixed;
    left: 50%;
    top:14%;
    padding: 1rem;
    background-color: white;
    border-radius: 5px;
    width: 60%;
    height: 80%;
    z-index: 400;

    -webkit-transform: translate(-50%, -0%);
    transform: translate(-50%, -0%);

    -webkit-animation-name: ComparisonModal__modal_animation__3lraC;

            animation-name: ComparisonModal__modal_animation__3lraC;
    -webkit-animation-duration: 200ms;
            animation-duration: 200ms;
}

@-webkit-keyframes ComparisonModal__modal_animation__3lraC {
    0% { 
        width: 0%;
        height: 0%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    20% {
        width: 20%;
        height: 15%;
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    40% {
        width: 30%;
        height: 30%;
        -webkit-transform: translate(-50%, -60%);
