/*** modal styles ***/
.DeleteShareModal__modal_container__rl33E {
    position: fixed;
    left: 50%;
    top:20%;
    padding: 1rem;
    background-color: white;
    border-radius: 5px;
    width: 35%;
    max-width: 50%;
    height: 30%;
    max-height: 40vh;
    box-shadow: 1rem 1rem 3rem 2rem rgba(189, 186, 186, 0.445);
    z-index: 400;

    -webkit-transform: translate(-50%, -0%);
    transform: translate(-50%, -0%);

    -webkit-animation-name: DeleteShareModal__modal_animation__1qNO7;

            animation-name: DeleteShareModal__modal_animation__1qNO7;
    -webkit-animation-duration: 200ms;
            animation-duration: 200ms;
}

@-webkit-keyframes DeleteShareModal__modal_animation__1qNO7 {
    0% { 
        width: 0%;
        height: 0%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    20% {
        width: 20%;
        height: 15%;
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    40% {
        width: 30%;
        height: 30%;
        -webkit-transform: translate(-50%, -60%);
                transform: translate(-50%, -60%);
    };
    60% {
        width: 40%;
        height: 45%;
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    }; 
    50% {
        width: 50%;
        height: 60%;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }; 
    100% {
        width: 60%;
        height: 80%;
        -webkit-transform: translate(-50%, -0%);
                transform: translate(-50%, -0%);
    };

}

@keyframes DeleteShareModal__modal_animation__1qNO7 {
    0% { 
        width: 0%;
        height: 0%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    20% {
        width: 20%;
        height: 15%;
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    40% {
        width: 30%;
        height: 30%;
        -webkit-transform: translate(-50%, -60%);
                transform: translate(-50%, -60%);
    };
    60% {
        width: 40%;
        height: 45%;
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    }; 
    50% {
        width: 50%;
        height: 60%;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }; 
    100% {
        width: 60%;
        height: 80%;
        -webkit-transform: translate(-50%, -0%);
                transform: translate(-50%, -0%);
    };

}

