    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 700;
}

.MessageBoxModal__modal_closeButton__6oEwu {
    position: absolute;
    top: 0;
    right: -35px;
    border: none;
    background-color: rgba(255, 255, 255, 0);
}

.MessageBoxModal__modal_closeButton__6oEwu div {
    background-color: rgba(255, 255, 255, 0);
