.AuthModal__modal_container__3RfRv {
    position: fixed;
    left: 50%;
    top:27%;
    padding: 1rem;
    background-color: white;
    border-radius: 5px;
    width: 30%;
    max-width: 50%;
    height: auto;
    box-shadow: 1rem 1rem 3rem 2rem rgba(189, 186, 186, 0.445);
    z-index: 400;

    -webkit-transform: translate(-50%, -20%);
    transform: translate(-50%, -20%);

    -webkit-animation-name: AuthModal__modal_animation__12wLW;

            animation-name: AuthModal__modal_animation__12wLW;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
}

@-webkit-keyframes AuthModal__modal_animation__12wLW {
    0% { 
        -webkit-transform: translate(-50%, -150%); 
                transform: translate(-50%, -150%);
    };
    20% {
        -webkit-transform: translate(-50%, -120%);
                transform: translate(-50%, -120%);
    };
    40% {
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    60% {
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    80% {
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    };
    100% {
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    };

}

@keyframes AuthModal__modal_animation__12wLW {
    0% { 
        -webkit-transform: translate(-50%, -150%); 
                transform: translate(-50%, -150%);
    };
    20% {
        -webkit-transform: translate(-50%, -120%);
                transform: translate(-50%, -120%);
    };
    40% {
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    60% {
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    80% {
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    };
    100% {
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    };

}

