/*** modal styles ***/
.DeleteDataModal__modal_container__2UHIA {
    position: fixed;
    left: 50%;
    top:15%;
    padding: 1rem;
    background-color: white;
    border-radius: 5px;
    width: 40%;
    max-width: 50%;
    height: 60%;
    max-height: 60vh;
    box-shadow: 1rem 1rem 3rem 2rem rgba(189, 186, 186, 0.445);
    z-index: 400;

    -webkit-transform: translate(-50%, -0%);
    transform: translate(-50%, -0%);

    -webkit-animation-name: DeleteDataModal__modal_animation__3w2IM;

            animation-name: DeleteDataModal__modal_animation__3w2IM;
    -webkit-animation-duration: 200ms;
            animation-duration: 200ms;
}

@-webkit-keyframes DeleteDataModal__modal_animation__3w2IM {
    0% { 
        width: 0%;
        height: 0%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    20% {
        width: 20%;
        height: 15%;
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    40% {
        width: 30%;
        height: 30%;
        -webkit-transform: translate(-50%, -60%);
                transform: translate(-50%, -60%);
    };
    60% {
        width: 40%;
        height: 45%;
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    }; 
    50% {
        width: 50%;
        height: 60%;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }; 
    100% {
        width: 60%;
        height: 80%;
        -webkit-transform: translate(-50%, -0%);
                transform: translate(-50%, -0%);
    };

}

@keyframes DeleteDataModal__modal_animation__3w2IM {
    0% { 
        width: 0%;
        height: 0%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    20% {
        width: 20%;
        height: 15%;
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    40% {
        width: 30%;
        height: 30%;
        -webkit-transform: translate(-50%, -60%);
                transform: translate(-50%, -60%);
    };
    60% {
        width: 40%;
        height: 45%;
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    }; 
    50% {
        width: 50%;
        height: 60%;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }; 
    100% {
        width: 60%;
        height: 80%;
        -webkit-transform: translate(-50%, -0%);
                transform: translate(-50%, -0%);
    };

}

