/*** modal styles ***/
.BankDetailsModal__modal_container__177Wh {
    position: fixed;
    left: 50%;
    top:15%;
    padding: 1rem;
    background-color: white;
    border-radius: 5px;
    width: 60%;
    max-width: 70%;
    height: 80%;
    /* box-shadow: 1rem 1rem 3rem 2rem rgba(189, 186, 186, 0.445); */
    z-index: 400;

    -webkit-transform: translate(-50%, -0%);
    transform: translate(-50%, -0%);

    -webkit-animation-name: BankDetailsModal__modal_animation__2WA2W;

            animation-name: BankDetailsModal__modal_animation__2WA2W;
    -webkit-animation-duration: 200ms;
            animation-duration: 200ms;
}

@-webkit-keyframes BankDetailsModal__modal_animation__2WA2W {
    0% { 
        width: 0%;
        height: 0%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    20% {
        width: 20%;
        height: 15%;
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    40% {
        width: 30%;
        height: 30%;
        -webkit-transform: translate(-50%, -60%);
                transform: translate(-50%, -60%);
    };
    60% {
        width: 40%;
        height: 45%;
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    }; 
    50% {
        width: 50%;
        height: 60%;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }; 
    100% {
        width: 60%;
        height: 80%;
        -webkit-transform: translate(-50%, -0%);
                transform: translate(-50%, -0%);
    };

}

@keyframes BankDetailsModal__modal_animation__2WA2W {
    0% { 
        width: 0%;
        height: 0%;
        -webkit-transform: translate(-50%, -100%);
                transform: translate(-50%, -100%);
    };
    20% {
        width: 20%;
        height: 15%;
        -webkit-transform: translate(-50%, -80%);
                transform: translate(-50%, -80%);
    };
    40% {
        width: 30%;
        height: 30%;
        -webkit-transform: translate(-50%, -60%);
                transform: translate(-50%, -60%);
    };
    60% {
        width: 40%;
        height: 45%;
        -webkit-transform: translate(-50%, -40%);
                transform: translate(-50%, -40%);
    }; 
    50% {
        width: 50%;
        height: 60%;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }; 
    100% {
        width: 60%;
        height: 80%;
        -webkit-transform: translate(-50%, -0%);
                transform: translate(-50%, -0%);
    };

}

.BankDetailsModal__modal__1CDCF:before {
    content: "";
    position: fixed;
    background-color: rgba(0, 0, 0, 0.774);
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.BankDetailsModal__modal_closeButton__1lN6i {
    position: absolute;
    top: 0;
    right: -40px;
    border:none;
    background-color: rgba(0, 0, 0, 0);
    
   
}

.BankDetailsModal__modal_closeButton__1lN6i:focus {
    outline-color: #006AFF;
}

.BankDetailsModal__modal_closeButton__1lN6i:hover {
    cursor: pointer;
}

